<template>
  <div>
    <label :for="inputId">{{ label }}</label>
    <div class="input_box">
      <input
      v-model.trim="inputModel"
      @change="emitData"
      :type="type"
      :id="inputId"
      :placeholder="placeholder"
      :invalid="invalid"
      :class="invalid ? 'danger-pass-input' : ''"
      >
      <a @mousedown.prevent="textShow" @mouseup.prevent="textHide" v-if="showBtn">
        <img :src="require(`../assets/passwordInput/${toggleEye}.svg`)" alt="eye">
      </a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    label: String,
    placeholder: String,
    showBtn: Boolean,
    inputId: String,
    invalid: {
      type: Boolean,
      default: false
    }
  },
  // props: ['label', 'placeholder', 'showBtn', 'inputId', 'invalid'],
  emits: ['emitTextInput'],
  data () {
    return {
      inputModel: '',
      type: 'password'
    }
  },
  methods: {
    emitData () {
      this.$emit('emitTextInput', this.inputModel)
    },
    textShow () {
      this.type = 'text'
    },
    textHide () {
      this.type = 'password'
    }
  },
  computed: {
    toggleEye () {
      if (this.type === 'password') {
        return 'eye'
      } else {
        return 'closed_eye'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  div {
    display: flex;
    flex-direction: column;
    max-width: 380px;
  }
  label {
    font-size: 15px;
    color: #293148;
    opacity: 45%;
    margin-bottom: 10px;
  }
  .input_box {
    position: relative;
    .danger-pass-input {
      border: 2px solid #D90E32;
    }
    input {
    border-radius: 10px;
    border: 1px solid #E4E4E4;

    padding: 12px 16px;

    font-size: 16px;
      &:focus {
        border: 1px solid #7895FF;
        outline: 2px solid #C9D5FF;
      }
    }
  }
  a {
    position: absolute;
    right: 16px;
    top: 9px;
    margin: auto 0;
    img {
      height: 24px;
      width: 24px;
    }
  }
</style>
