<template>
  <div>
    <div class="profile_header">
      <span class="profile_title">Profile</span>
      <PersonPannel></PersonPannel>
    </div>
    <ProfileCard :data="profileCardAdminData"></ProfileCard>
    <div class="profile_data_box" v-if="!openedSettings">
      <span class="profile_data__item">Username:<span>{{ getAdminProfileData.username }}</span></span>
      <span class="profile_data__item">Email address:<span>{{ stateAdminAuthData.email}}</span></span>
      <span class="profile_data__item">Contact phone:<span>{{ getAdminProfileData.phone }}</span></span>
      <AppButton text="Change settings" :type="'default'" @action="openedSettings = true"></AppButton>
    </div>
    <form action="" class="profile_settings" v-if="openedSettings">
      <TextInput
        @emitTextInput="settingsUsernameChange"
        label="Username"
        placeholder="Type name here..."
        inputId="profileSettingsUsernamme"
        :model="stateAdminProfileData.username"
        ></TextInput>
      <TextInput
        @emitTextInput="settingsEmailChange"
        label="Email"
        placeholder="Type email here..."
        inputId="profileSettingsEmail"
        :model="stateAdminAuthData.email"
        ></TextInput>
      <TextInput
        @emitTextInput="settingsPhoneChange"
        label="Contact phone"
        placeholder="Type phone here..."
        inputId="profileSettingsPhone"
        :model="stateAdminProfileData.phone"
        ></TextInput>
      <a href="#" class="profile_settings__pass_link" @click.prevent="openedChangePass = true">Change password</a>
      <div v-if="openedChangePass" class="profile_settings__change_pass_box">
        <PasswordInput
          @emitTextInput="settingsOldPasswordChange"
          :showBtn="false" label="Old password"
          placeholder="Type password here..."
          :invalid="stateValidation.profilePasswordOld"
          ></PasswordInput>
        <PasswordInput
          @emitTextInput="settingsNewPasswordModel1Change"
          :showBtn="false" label="New password"
          placeholder="Type password here..."
          :invalid="stateValidation.profilePasswordNew"
          ></PasswordInput>
        <PasswordInput
          @emitTextInput="settingsNewPasswordModel2Change"
          :showBtn="false" label="Replay new pasword"
          placeholder="Type password here..."
          :invalid="stateValidation.profilePasswordNew"
          ></PasswordInput>
        <a href="#" class="change_pass_boxs__link" @click.prevent="saveChangePassword({
          old: settingsOldPasswordModel,
          new1: settingsNewPasswordModel1,
          new2: settingsNewPasswordModel2
        })">Save change</a>
      </div>
      <AppButton text="save change" :type="'danger'" @action="saveChangeProfile({
        username: changeParamModel (settingsUsernameModel, stateAdminProfileData.username),
        email: changeParamModel (settingsEmailModel, stateAdminAuthData.email),
        phone: changeParamModel (settingsPhoneModel, stateAdminProfileData.phone)
      })"></AppButton>
    </form>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'

import ProfileCard from '@/components/ProfileCard.vue'
import AppButton from '@/components/AppButton.vue'
import TextInput from '@/components/TextInput.vue'
import PasswordInput from '@/components/PasswordInput.vue'
import PersonPannel from '@/components/PersonPannel.vue'
export default {
  data () {
    return {
      settingsUsernameModel: '',
      settingsEmailModel: '',
      settingsPhoneModel: '',
      settingsOldPasswordModel: '',
      settingsNewPasswordModel1: '',
      settingsNewPasswordModel2: '',
      openedSettings: false,
      openedChangePass: false
    }
  },
  computed: {
    ...mapGetters({
      getAdminProfileData: 'adminProfileData'
    }),
    ...mapState({
      stateAdminProfileData: 'adminProfileData',
      stateAdminAuthData: 'adminAuthData',
      stateValidation: 'validation'
    }),
    profileCardAdminData () {
      return {
        avatar: this.stateAdminProfileData.avatar,
        username: this.stateAdminProfileData.username,
        credits: this.stateAdminProfileData.credits,
        tokens: this.stateAdminProfileData.tokens,
        status: 'active'
      }
    }
  },
  methods: {
    ...mapMutations(['changeAdminDataState']),
    settingsUsernameChange (data) {
      this.settingsUsernameModel = data
    },
    settingsEmailChange (data) {
      this.settingsEmailModel = data
    },
    settingsPhoneChange (data) {
      this.settingsPhoneModel = data
    },
    settingsOldPasswordChange (data) {
      this.settingsOldPasswordModel = data
    },
    settingsNewPasswordModel1Change (data) {
      this.settingsNewPasswordModel1 = data
    },
    settingsNewPasswordModel2Change (data) {
      this.settingsNewPasswordModel2 = data
    },
    changeParamModel (model, value) {
      if (model === '') {
        return value
      } else {
        return model
      }
    },
    saveChangeProfile (data) {
      this.changeAdminDataState({
        username: data.username,
        email: data.email,
        phone: data.phone
      })
      this.openedSettings = false
    },
    saveChangePassword (data) {
      if (data.old === this.stateAdminAuthData.password) {
        if (data.new1 === data.new2 && data.new1 !== '') {
          this.stateAdminAuthData.password = data.new1
          this.openedChangePass = false
          console.log(this.stateAdminAuthData.password)
        } else {
          this.stateValidation.profilePasswordOld = false
          this.stateValidation.profilePasswordNew = true
        }
      } else {
        this.stateValidation.profilePasswordOld = true
      }
    }
  },
  components: {
    ProfileCard,
    AppButton,
    TextInput,
    PasswordInput,
    PersonPannel
  },
  onMounted () {
    console.log(this.profileCardAdminData)
  }
}
</script>
